tB;EACE,YAAY;EACZ,iBAAiB;EACjB,uBAAuB;EACvB,2BAA2B;EAC3B,mBAAmB;EACnB,kBAAkB;EAClB,YAAY;EACZ,wBAAwB;EACxB,aAAa;EACb,gCAAgC;AAClC;;AAEA;EACE,yBAAyB;EACzB,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,UAAU;EACV,cAAc;AAChB;;AAEA;EACE,yBAAyB;EACzB,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,0BAA0B;EAC1B,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;;;EAGE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;;EAEE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,sCAAsC;EACtC,kBAAkB;EAClB,iBAAiB;EACjB,mBAAmB;EACnB,YAAY;EACZ,gCAAgC;EAChC,YAAY;EACZ,6BAA6B;AAC/B;;AAEA;EACE,qCAAqC;AACvC;;AAEA,mBAAmB;;AAEnB;EACE,YAAY;EACZ,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,wBAAwB;EACxB,2BAA2B;EAC3B,eAAe;AACjB;;AAEA;EACE,0BAA0B;EAC1B;;AAEF;EACE,cAAc;EACd,aAAa;AACf","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&family=EB+Garamond:ital,wght@0,400..800;1,400..800&family=Lora:ital,wght@0,400..700;1,400..700&family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');\n\n/* CSS Reset */\n*, *::before, *::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\n\ninput, button, textarea, select {\n  font: inherit;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\nh1, h2, h3, h4, h5, h6 {\n  text-wrap: balance;\n}\n\n#root, #__next {\n  isolation: isolate;\n}\n\n/*Reset Over */\n/* General Styling */\n\n:root {\n  --white-rose: #ffe4e6;\n  --very-light-rose: #fda4af;\n  --light-rose: #fb7185;\n  --rose: #f43f5e;\n  --dark-rose: #e11d48;\n  --very-dark-rose: #be123c;\n  --bg-dark: #881337;\n  --bg-very-dark: #4c0519;\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  min-width: 80rem;\n  font-family: \"EB Garamond\", \"Cormorant Garamond\", \"Lora\", 'Times New Roman', Times, serif;\n}\n\n/* Header */\n\nheader {\n  display: flex;\n  align-items: center;\n  background-color: var(--bg-very-dark);\n  height: 5.5rem;\n  justify-content: center;\n}\n\n.logo-container {\n  display: flex;\n  gap: 1rem;\n  align-items: center;\n  width: 40rem;\n  text-decoration: none;\n}\n\n.logo {\n  width: auto;\n  height: 5.5rem;\n}\n\n.header-text {\n  color: white;\n  text-decoration: none;\n}\n\nnav {\n  display: flex;\n  margin-left: -10rem;\n}\n\n.nav-button {\n  cursor: pointer;\n  background-color: var(--bg-very-dark);\n  transition: background-color 0.3s ease;\n  color: white;\n  border: none;\n  width: 7rem;\n  height: 5.5rem;\n  font-size: 1.5rem;\n}\n\n.nav-button:hover {\n  background-color: var(--very-dark-rose);\n}\n\n/* Content - Home */\n\n#content {\n  display: flex;\n  flex-direction: column;\n  background-color: var(--bg-very-dark);\n}\n\n/* Content - Home - Hero */\n\n.hero-container {\n  width: 100%;\n  height: 32rem;\n  background-color: #4c0519;\n  position: relative;\n  display: grid;\n}\n\n.hero-bg {\n  width: auto;\n  height: 30rem;\n  position: absolute;\n  top: 0;\n  margin-left: 18rem;\n  z-index: 0;\n  object-fit: cover;\n}\n\n.copy-container {\n  position: absolute;\n  z-index: 1;\n  width: 30rem;\n  background-color: white;\n  border: none;\n  grid-column: 2 / -1;\n  border-radius: 25px;\n  padding: 1.5rem;\n  justify-self: center;\n  align-self: center;\n  margin-left: 20rem;\n  margin-bottom: 10rem;\n}\n\n.kicker-container {\n  display: flex;\n  align-items: center;\n  gap: 0.5rem;\n  overflow: hidden;\n  height: 1rem;\n}\n\n.kicker-icon {\n  width: 3rem;\n  height: auto;\n  overflow: hidden;\n}\n\n.kicker-text {\n  font-size: 0.8rem;\n}\n\n.main-copy {\n  font-size: 2.5rem;\n  line-height: 0.9;\n}\n\n.secondary-copy {\n  font-size: 0.9rem;\n  line-height: 1.2;\n  margin-top: 0.5rem;\n}\n\n.cta-button {\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n  margin-top: 0.5rem;\n  font-size: 1.4rem;\n  border-radius: 25px;\n  border: none;\n  padding: 0.2rem 1rem 0.2rem 1rem;\n  color: white;\n  background-color: var(--rose);\n}\n\n.cta-button:hover {\n  background-color: var(--bg-dark);\n}\n\n.hero-img-credit {\n  position: absolute;\n  color: white;\n  justify-self: center;\n  bottom: 0.5rem;\n  font-size: 0.8rem;\n  margin-right: 16rem;\n}\n\n.hero-img-credit a {\n  color: var(--rose);\n  text-decoration: none;\n  transition: color 0.3s ease;\n  \n}\n\n.hero-img-credit a:hover {\n  color: var(--light-rose);\n}\n\n/* Content - Home - Review */\n\n.review-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 2rem;\n  background-color: white;\n  padding-bottom: 2rem;\n}\n\n.review-header {\n  font-size: 2.2rem;\n  margin-top: 2rem;\n  font-weight: bold;\n}\n\n.customer-container {\n  display: grid;\n  grid-template: 1fr 3fr / 1fr 4fr;\n  width: 40rem;\n  padding: 1rem;\n  border: 2px solid var(--rose);\n  border-radius: 25rem;\n}\n\n.customer-img {\n  grid-area: 1 / 1 / -1 / 2;\n  width: 7rem;\n  height: auto;\n  overflow: hidden;\n  border-radius: 50%;\n}\n\n.customer-name {\n  font-size: 1.2rem;\n  margin-left: 1rem;\n}\n\n.customer-img-credit {\n  font-size: 0.8rem;\n  display: inline;\n  margin-left: 11rem;\n  font-weight: 500;\n}\n\n.customer-img-credit > a {\n  text-decoration: none;\n  color: var(--rose);\n  transition: color 0.3s ease;\n}\n\n.customer-img-credit > a:hover {\n  color: var(--very-dark-rose);\n}\n\n.customer-review {\n  line-height: 1.2;\n  margin-left: 1rem;\n}\n\n/* Content - Menu */\n\n.menu-container {\n  width: 55rem;\n  min-height: 20rem;\n  background-color: white;\n  border: 4px solid --bg-dark;\n  border-radius: 25px;\n  align-self: center;\n  margin: 3rem;\n  padding: 2rem;\n  display: grid;\n  grid-template: 0.9fr repeat(3, 1fr) 1fr / 1fr 1fr;\n}\n\n.menu-header {\n  grid-area: 1 / 1 / 2 / -1;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  height: 10rem;\n}\n\n.menu-divider {\n  width: 30rem;\n  height: auto;\n}\n\n.menu-header-text {\n  font-size: 2.5rem;\n  padding: 0;\n  line-height: 1;\n  font-weight: 500;\n}\n\n.food-container {\n  display: grid;\n  grid-template: 3rem 1.5rem 7rem / 1fr 1fr;\n  padding: 1rem;\n}\n\n.food-header {\n  line-height: 1;\n  grid-area: 1 / 1 / 2 / 2;\n}\n\n.food-price {\n  line-height: 1;\n  grid-area: 2 / 1 / 3 / 2;\n}\n\n.food-text {\n  line-height: 1;\n  grid-area: 3 / 1 / 4 / 2;\n  font-size: 0.8rem;\n  padding-right: 0.3rem;\n}\n\n.food-img {\n  width: 100%;\n  height: auto;\n  grid-area: 1 / 2 / -1 / -1;\n  border-radius: 50%;\n}\n\n.menu-misc-container {\n  grid-area: -2 / 1 / -1 / -1;\n  display: grid;\n  grid-template: 0.8fr 4fr / 1fr 1fr;\n}\n\n.menu-divider-bottom {\n  grid-area: 1 / 1 / 2 / -1;\n  width: 30rem;\n  justify-self: center;\n}\n\n.alcohol-menu-container {\n  grid-area: 2 / 1 / -1 / 2;\n  display: grid;\n  grid-template: 1fr 5fr / 3fr 1fr;\n  padding: 1rem;\n}\n\n.alcohol-text {\n  grid-area: 2 / 1 / -1 / 2;\n  line-height: 1.5;\n  font-size: 0.8rem;\n  white-space: pre-line;\n  padding-top: 0.3rem;\n}\n\n.alcohol-prices {\n  grid-area: 2 / 2 / -1 / -1;\n  line-height: 1.5;\n  font-size: 0.8rem;\n  white-space: pre-line;\n  padding-top: 0.3rem;\n  justify-self: end;\n}\n\n.beverage-menu-container {\n  grid-area: 2 / 2 / -1 / 3;\n  display: grid;\n  grid-template: 1fr 5fr / 3fr 1fr;\n  padding: 1rem;\n}\n\n.bev-header {\n  line-height: 1;\n  padding-left: 2rem;\n}\n\n.bev-text {\n  grid-area: 2 / 1 / -1 / 2;\n  line-height: 1.5;\n  font-size: 0.8rem;\n  white-space: pre-line;\n  padding-top: 0.3rem;\n  padding-left: 2rem;\n}\n\n/* Content - About */\n\n.about-page-container {\n  display: flex;\n  flex-direction: column;\n  background-color: white;\n}\n\n.about-restaurant-container {\n  width: 100%;\n  height: 30rem;\n  background-color: white;\n  display: grid;\n  grid-template-columns: 30rem 30rem;\n  justify-content: center;\n  margin-top: 3rem;\n}\n\n.restaurant-img {\n  border-radius: 25px 0 0 0;\n}\n\n.about-text-container {\n  background-color: var(--bg-very-dark);\n  border-radius: 0 25px 0 0;\n  display: flex;\n  flex-direction: column;\n  padding: 2rem;\n}\n\n.about-text-header-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: auto;\n}\n\n.divider-about {\n  width: 16rem;\n  height: auto;\n}\n\n.about-text-header {\n  color: white;\n  font-weight: 500;\n  line-height: 1;\n  padding: 0.3rem 0 0.6rem 0;\n}\n\n.about-text {\n  color: white;\n  padding-top: 1rem;\n  white-space: pre-line;\n  font-size: 0.95rem;\n}\n\n.chef-container {\n  width: 100%;\n  height: 30rem;\n  background-color: white;\n  display: grid;\n  grid-template-columns: 30rem 30rem;\n  justify-content: center;\n  margin-bottom: 3rem;\n}\n\n.chef-text-container {\n  background-color: var(--bg-very-dark);\n  border-radius: 0 0 0 25px;\n  padding: 2rem;\n}\n\n.chef-img {\n  border-radius: 0 0 25px 0;\n}\n\n/* Content - Contact */\n.contact-container {\n  width: 55rem;\n  min-height: 20rem;\n  background-color: white;\n  border: 4px solid --bg-dark;\n  border-radius: 25px;\n  align-self: center;\n  margin: 3rem;\n  padding: 0 2em 2rem 2rem;\n  display: grid;\n  grid-template: 1fr 2fr / 1fr 1fr;\n}\n\n.contact-header-container {\n  grid-area: 1 / 1 / 2 / -1;\n  justify-items: center;\n  padding: 1.75rem;\n}\n\n.contact-header-text {\n  font-size: 2.5rem;\n  font-weight: 500;\n  padding: 0;\n  line-height: 1;\n}\n\n.contact-img {\n  grid-area: 2 / 1 / -1 / 2;\n  border-radius: 25px;\n  margin-bottom: 1rem;\n}\n\n.contact-info-container {\n  grid-area: 2 / 2 / -1 / -1;\n  padding: 2rem;\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n}\n\n.address-container,\n.tel-container,\n.email-container {\n  display: flex;\n  flex-direction: column;\n}\n\n.contact-text {\n  white-space: pre-line;\n  line-height: 1.2;\n}\n\n.tel-num,\n.email-text {\n  line-height: 0.8;\n}\n\n.contact-button-container {\n  display: flex;\n  margin-top: 0.5rem;\n}\n\n.contact-info-button {\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n  margin-top: 0.5rem;\n  font-size: 1.7rem;\n  border-radius: 25px;\n  border: none;\n  padding: 0.2rem 1rem 0.2rem 1rem;\n  color: white;\n  background-color: var(--rose);\n}\n\n.contact-info-button:hover {\n  background-color: var(--bg-very-dark);\n}\n\n/* Footer Styling */\n\nfooter {\n  height: 4rem;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--bg-dark);\n}\n\n.credit {\n  display: flex;\n  align-items: center;\n  gap: 0.3rem;\n  color: white;\n  font-size: 1.2rem;\n}\n\nfooter a {\n  text-decoration: none;\n  color: var(--white-rose);\n  transition: color 0.3s ease; \n  cursor: pointer;\n}\n\nfooter a:hover {\n  color: var(--bg-very-dark);\n  }\n\n.credit img {\n  height: 1.2rem;\n  width: 1.2rem;\n}"],"sourceRoot":""}]);
